
html {
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}
html::-webkit-scrollbar {
  width: 0;
}
body, div, span, header, footer, nav, section, aside, article, ul, li, a, p, textarea, button, input, select {
  padding: 0;
  margin: 0;
  list-style: none;
  font-weight: normal;
  font-style: normal;
  text-decoration: none;
  border: none;
  font-family: 'Hiragino Sans GB', 'Hiragino Sans GB W3', 'Microsoft YaHei', 'WenQuanYi Micro Hei', sans-serif;
  -webkit-tap-highlight-color: transparent;
}
body:focus, div:focus, span:focus, header:focus, footer:focus, nav:focus, section:focus, aside:focus, article:focus, ul:focus, li:focus, a:focus, p:focus, textarea:focus, button:focus, input:focus, select:focus {
    outline: none;
}
html {
  -webkit-overflow-scrolling: touch !important;
  overflow: auto !important;
  height: 100% !important;
}
body {
  -webkit-overflow-scrolling: touch !important;
  overflow: auto !important;
  position: relative;
  height: 100% !important;
  width: 100%;
  background: #f9f9f9;
  font-size: 12px;
}
.clearfix {
  zoom: 1;
}
.clearfix::after {
    visibility: hidden;
    display: block;
    font-size: 0;
    content: " ";
    clear: both;
    height: 0;
}
.vertical-middle {
  height: 100%;
  width: 0;
  display: inline-block;
  vertical-align: middle;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.mt6 {
  margin-top: 6px;
}
.ml60 {
  margin-left: 60px;
}
.pointer {
  cursor: pointer;
}
[v-cloak] {
  display: none;
}
.slide-fade-enter-active {
  transition: all .6s ease;
}
.slide-fade-leave-active {
  transition: all .5s ease;
}
.slide-fade-enter,
.slide-fade-leave-active {
  transform: translateX(60px);
  opacity: 0;
}
body, div, span, header, footer, nav, section, aside, article, ul, li, a, p, h1, h2, h3, h4, h5, i, b, textarea, button, input, select {
  box-sizing: border-box;
}
.icon {
  display: inline-block;
  overflow: hidden;
  width: 16px;
  height: 16px;
  font-size: 0;
  line-height: 0;
  cursor: pointer;
  margin-left: 1px;
  margin-top: 1px;
  background: url("../img/icons.png") 0 0 no-repeat;
}
.icon.icon-source {
    background-position: 0 0px;
}
.icon.icon-undo {
    background-position: 0 -48px;
}
.icon.icon-redo {
    background-position: 0 -64px;
}
.icon.icon-bold {
    background-position: 0 -416px;
}
.icon.icon-italic {
    background-position: 0 -432px;
}
.icon.icon-underline {
    background-position: 0 -448px;
}
.icon.icon-strikethrough {
    background-position: 0 -464px;
}
.icon.icon-lineheight {
    background-position: 0 -992px;
}
.icon.icon-removeformat {
    background-position: 0 -480px;
}
.icon.icon-checked {
    background-position: 0 -944px;
}
.e-edit-utils {
  background-color: #F0F0EE;
  border-bottom: 1px solid #CCC;
  padding: 4px 10px;
  font-size: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: left;
      justify-content: left;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.e-edit-utils > li {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: left;
        justify-content: left;
    -ms-flex-align: center;
        align-items: center;
    margin-right: 4px;
}
.e-edit-utils > li > a {
      padding: 1px;
}
.e-edit-utils > li > a.active, .e-edit-utils > li > a:hover {
        border: 1px solid #5690D2;
        background-color: #E9EFF6;
        padding: 0;
}
.e-edit-utils-separator {
    font-size: 0;
    display: inline-block;
    height: 16px;
    width: 0;
    border-left: 1px solid #A0A0A0;
    border-right: 1px solid #FFFFFF;
    margin-left: 4px;
}
.e-edit-content {
  word-wrap: break-word;
  word-break: break-all;
  overflow: auto;
  resize: vertical;
  height: 300px;
  border: 1px solid #CCC;
  width: 100%;
  padding: 10px;
}
.own-class {
  background-color: red;
}



